import React, { useEffect } from 'react';
import Aos from 'aos';
import 'aos/dist/aos.css';

import '../App.css';

// IMPORT VIDEO & IMG
import web from '../img/web.png';
import discord from '../img/discord.png';

// IMPORT COMPONENTS
import Navbar from '../components/navbar';
import Footer from '../components/footer';
import Card from '../components/card';

function Shop() {

  useEffect(() => {
    Aos.init({duration: 1500});
  }, [])

  const btnRedirect = () => {
    window.location.href = "https://discord.com/invite/c5zTGXJqg6";
  }

  return (
    <>
      <div className="App">
        <header>
          <Navbar />
        </header>

        {/* SHOP */}
        <div className="sectionMain" >
          <div className="container" style={{paddingTop: "250px"}} data-aos="fade-up">
            <h1>
                Ti serve un <span>sito web</span> o un <span>bot discord</span>? <br></br>
                per pi informazioni guarda qui sotto.
            </h1>
          </div>

        </div>

        <div className="section d-block" style={{backgroundColor: "#13131F"}}>
         <div class="container text-center" style={{paddingTop: "100px"}}>
            <div class="row">
              <div class="col my-5 d-flex justify-content-center" data-aos="fade-up">
                <Card 
                  img = {web}
                  title="SITO WEB"
                  desc = "Sito web personalizzato con host incluso."
                  style = {{paddingLeft: "40px", paddingRight: "40px"}}
                  btnTitle = "Crea sito web"
                  click = {btnRedirect}
                />
              </div>
              <div class="col my-5 d-flex justify-content-center" data-aos="fade-up" data-aos-delay="400">
                <Card
                  img = {discord}
                  title="BOT DISCORD"
                  desc = "Bot discord personalizzato con con host incluso."
                  style = {{paddingLeft: "40px", paddingRight: "40px"}}   
                  btnTitle = "Crea bot discord" 
                  click = {btnRedirect}
                />
              </div>
            </div>
          </div>
        </div>

       

      </div>
      <footer>
          <Footer />
      </footer>
    </>
  );
}

export default Shop;
