import { toBePartiallyChecked } from "@testing-library/jest-dom/matchers";
import { Component } from "react";

class Card extends Component {
    render(){
        return(
          <>
            <div className="crd" style={{width: "18rem"}}>
            <img src={this.props.img} className="card-img-top" style={this.props.style} alt="Reload the page." />
            <div className="card-body">

                <a target="_blank" href={this.props.link} className="a"><h5 className="card-title">{this.props.title}</h5></a>

                <hr></hr>

                <p className="card-text">{this.props.desc}</p>

                <div className="container d-flex justify-content-center" style={this.props.data}>
                  <button type="button" class="bottone" onClick={this.props.click} >{this.props.btnTitle}</button>
                </div>
            </div>
            </div>


          </>
        );
    } 
}

export default Card;