import React, { useEffect } from 'react';
import { Link } from "react-router-dom";

import Aos from 'aos';
import 'aos/dist/aos.css';
import { ReactTyped } from 'react-typed';

import '../App.css';

// IMPORT VIDEO & IMG
import LG from '../img/logoLG.png';

// IMPORT COMPONENTS
import Navbar from '../components/navbar';
import Footer from '../components/footer';
import Card from '../components/card';
import TimeLine from '../components/timeline';

function Home() {
  useEffect(() => {
    Aos.init({duration: 1500});
  }, [])

  return (
    <>
      <div className="App">
        <header>
          <Navbar />
        </header>

        {/* HOME */}
        <div className="sectionMain" style={{paddingTop: "300px"}} >
          <div className="text-center d-flex justify-content-center">
            <h1 data-aos="fade-up">Ciao sono <span>lori</span> e sono un <br></br>
              <span data-aos="zoom-in" data-aos-delay="900">
                 <ReactTyped
                  strings={[
                    "Web developer",
                    "Bot developer"
                  ]}
                  typeSpeed={90}
                  backSpeed={90}
                  loop
                />
              </span>
            </h1>
          </div>
          <h1 className="arrow text-center my-5"><a href="#AboutMe" className="arrow"></a></h1>
        </div>



        {/* TIME LINE */}
        <div className="section" style={{backgroundColor: "#13131F", paddingTop: "50px"}} id="AboutMe">
          <TimeLine />
        </div>

        <div className="section" style={{backgroundColor: "#13131F"}} id="Projects">
          <div class="container text-center" style={{marginTop: "150px"}}>
              <h2 className="text-start my-5" data-aos="fade-right">Projects</h2>
            <div class="row">
              <div class="col">
                
              </div>
              <div class="col my-3 d-flex justify-content-center" data-aos="slide-up" data-aos-delay="300">
                <Card
                  img = {LG}
                  title = "LORI GRAPHICS "
                  link = "https://lorigraphics.web.app/"
                  style = {{width: "170px", height: "220px", marginLeft: "50px"}}
                />
              </div>
              <div class="col">
                
              </div>
            </div>
          </div>

        </div>

      </div>
      <footer>
          <Footer />
      </footer>
    </>
  );
}

export default Home;
