function TimeLine() {
    return(
        <div className="section" style={{paddingBottom: "50px"}}>
        <div className="timeline">

          <div className="timeline-container left" data-aos="zoom-in" data-aos-delay="100">
            <div className="text-box">
              <h2>L'inizio</h2>
              <small>2018</small>
              <p>
                I primi programmi con python
              </p>
              <span className="left-arrow"></span>
            </div>
          </div>

          <div className="timeline-container right" data-aos="zoom-in" data-aos-delay="300">
            <div className="text-box">
              <h2>C++ / C#</h2>
              <small>2020</small>
              <p>
                Primi programmi in C++ e giochi con C#
              </p>
              <span className="right-arrow"></span>
            </div>
          </div>

          <div className="timeline-container left" data-aos="zoom-in" data-aos-delay="600">
            <div className="text-box">
              <h2>Primi siti web</h2>
              <small>2021-2023</small>
              <p>
                Primi siti web con html e css, poi dopo aver approfondito html, css e java sono passato a react
              </p>
              <span className="left-arrow"></span>
            </div>
          </div>

          <div className="timeline-container right" data-aos="zoom-in" data-aos-delay="600">
            <div className="text-box">
              <h2>Ora</h2>
              <small>2024</small>
              <p>
                Creazione di <a className="link" target='_blank' href="https://discord.com/invite/c5zTGXJqg6">Lori Graphics</a> dove vendo siti web e bot discord
              </p>
              <span className="right-arrow"></span>
            </div>
          </div>

        </div>
    </div>
    );
}

export default TimeLine;