import React, { useState, useEffect } from "react";
import { Outlet, Link, Navigate } from "react-router-dom";

import badge from '../img/badge-removebg-preview.png';

function Navbar() {

    const [scrollClass, setScrollClass] = useState("");
    const [navClick, setNavClick] = useState(false);

    useEffect(() => {
      window.addEventListener("scroll", handleScroll);
      return () => {
        window.removeEventListener("scroll", handleScroll);
      };
    }, []);
  
    const handleScroll = () => 
    {
      const scrollPosition = window.scrollY;
      if (scrollPosition > 500) 
      {
        setScrollClass("scrolled");
      } else 
      {
        setScrollClass("notscrolled");
      }
    };

    const handlenavClick = () =>{
      setNavClick(!navClick);
    };

    const handleTitle = () => {
      Navigate("/shop");
      window.location.reload();
    }
    

    return(
      <>
        <div className="container">
            <nav className={`navbar fixed-top p-2 ${scrollClass} ${navClick ? 'scrolled' : 'notscrolled'}`}>
                <div className="container-fluid">
                    <Link className="navbar-brand" to="/">
                        <img src="https://cdn.discordapp.com/avatars/980889160317542410/345178d7015edc356afa8aeb7848f55e.webp?size=240" title="lori______" className="ftp" alt="LORI" width="40" height="40" />
                        <img src={badge} className="" alt="LORI" width="120" height="40" usemap="#image-map" />
                    </Link>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation" onClick={handlenavClick}>
                        <svg xmlns="http://www.w3.org/2000/svg" width="30" height="30" fill="currentColor" className="bi bi-list" viewBox="0 0 16 16">
                            <path fill-rule="evenodd" d="M2.5 12a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5m0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5m0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5"/>
                        </svg>
                    </button>
                    <div className="collapse navbar-collapse text-center" id="navbarNav">
                        <ul className="navbar-nav">
                            <li className="nav-item">
                              <Link className="nav-link" to="/" style={{textDecoration: "none", fontSize: "20px", color: "white"}}>Home</Link>
                            </li>
                            <li className="nav-item">
                              <a className="nav-link" href="#AboutMe" style={{textDecoration: "none", fontSize: "20px", color: "white"}}>About me</a>
                            </li>
                            <li className="nav-item">
                              <a className="nav-link" href="#Projects" style={{textDecoration: "none", fontSize: "20px", color: "white"}}>Projects</a>
                            </li>
                            <li className="nav-item">
                              <Link to="/shop" onClick={handleTitle} style={{textDecoration: "none", fontSize: "20px", color: "white"}}>Shop</Link>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        </div>

        {/* IMAGE MAP */}
        <map name="image-map" style={{cursor: "pointer"}}>
          <area
            target="" 
            alt="HypeSquad Balance" 
            title="HypeSquad Balance" 
            coords="25,23,14" 
            shape="circle"          
          />

          <area
            target="" 
            alt="Active Developer" 
            title="Active Developer" 
            coords="55,23,14" 
            shape="circle"
          />
        </map>

        <Outlet />
      </>
    );
}

export default Navbar;